 /* ベース */
    * {
      box-sizing: border-box;
    }

    body {
      font-family: 'Inter', sans-serif;
      background: linear-gradient(135deg, #FFF7E3, #FDEBD3);
      margin: 0;
      padding: 20px;
      text-align: center;
    }

    h1 {
      margin-bottom: 16px;
      font-size: 24px;
    }

    /* トグルボタン */
    #toggleFormBtn {
      padding: 12px 20px;
      background: linear-gradient(135deg, #F4C06C, #E9A36A);
      color: white;
      border: none;
      border-radius: 8px;
      font-size: 16px;
      cursor: pointer;
      margin-bottom: 16px;
      transition: all 0.3s ease;
    }

    #toggleFormBtn:hover {
      transform: scale(1.05);
    }

    /* 入力フォーム */
    #urlForm {
      background: #fff;
      padding: 16px 24px;
      border-radius: 12px;
      box-shadow: 0 0 10px rgba(0, 0, 0, 0.06);
      margin: 0 auto 24px;
      display: flex;
      flex-direction: column;
      gap: 12px;
      max-width: 600px;
      width: 100%;
    }

    #urlForm input {
      padding: 10px 12px;
      font-size: 14px;
      border: 1px solid #E9A36A;
      border-radius: 8px;
      background-color: #FFF7E3;
      width: 100%;
      box-shadow: inset 1px 1px 3px rgba(0, 0, 0, 0.05);
    }

    #urlForm input:focus {
      background: #fff;
      outline: none;
      box-shadow: 0 0 6px rgba(228, 115, 80, 0.3);
    }

    #urlForm button {
      padding: 10px 25px;
      font-size: 15px;
      background: linear-gradient(135deg, #F4C06C, #E9A36A);
      color: white;
      border: none;
      border-radius: 8px;
      cursor: pointer;
      box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
      width: 100%;
      transition: all 0.3s ease;
        grid-column: span 3;
  width: auto;
  max-width: 100%;
  margin: 0 auto;           /* 中央寄せの決定版 */
  display: block;           /* blockにしないとmargin: autoが効かない場合も */
  text-align: center;
    }

    #urlForm button:hover {
      background: linear-gradient(135deg, #E9A36A, #D67D6A);
      transform: scale(1.05);
    }

    @media (min-width: 601px) {
      #urlForm {
        display: grid;
        grid-template-columns: repeat(3, 1fr);
        gap: 12px;
        max-width: 800px;
      }

      #urlForm input {
        width: 100%;
      }

      #urlForm button {
        grid-column: span 3;
        justify-self: center;
        width: fit-content;
        padding: 12px 40px;
      }
    }

    /* 一覧表示 */
    #urlList {
      list-style: none;
      padding: 0;
      margin: 0 auto;
      max-width: 960px;
    }

    #urlList li {
      background: white;
      border-radius: 10px;
      padding: 12px;
      margin: 12px auto;
      display: flex;
      align-items: center;
      gap: 16px;
      box-shadow: 0 0 6px rgba(0, 0, 0, 0.08);
    }

    #urlList img {
      width: 80px;
      height: 80px;
      object-fit: cover;
      border-radius: 8px;
    }

    #urlList a {
      flex: 1;
      font-weight: bold;
      font-size: 16px;
      color: #E76F51;
      text-decoration: none;
      text-align: left;
    }

    .btn-delete {
      padding: 8px 12px;
      background: #F4C06C;
      color: white;
      border: none;
      border-radius: 6px;
      cursor: pointer;
      font-size: 14px;
    }

    .btn-delete:hover {
      background: #D67D6A;
    }

    @media (min-width: 601px) {
      #urlList {
        display: grid;
        grid-template-columns: repeat(2, 1fr);
        gap: 16px;
      }
    }